@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  padding-top: 50px;
  padding-bottom: 200px;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", sans-serif;
  background-color: white;
  box-sizing: border-box;
  padding-left: 50px;
  padding-right: 50px;
  min-height: 100%;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  font-size: 1.5rem;
  background-color: black;
  color: white;
  height: 50px;
  display: flex;
  align-content: center;
  text-indent: 10px;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  display: flex;
  background-color: lightgrey;
  color: black;
  align-items: center;
  justify-content: center;
  height: 100px;
}

.square {
  width: 10px;
  height: 10px;
  background-color: black;
  margin-left: 10px;
}

.circle {
  width: 10px;
  height: 10px;
  background-color: black;
  border-radius: 50%;
  margin-right: 10px;
}

.triangle {
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 10px solid black;
}

